import React, { Component } from 'react';


class Arabic extends Component {

    state = {phone: false, grams: 100}

    gramHandle = (event) => {
        this.setState({grams: event.target.value});
    }

    calc = (x) => {
        return(
        ((x * this.state.grams) / 100).toFixed(1)
        )
    }

    calcKcal = (x) => {
        return(
        ((x * this.state.grams) / 100).toFixed(0)
        )
    }

    static getDerivedStateFromProps(props, state) {
        let isPhone = window.innerWidth < 600? true : false;
        return {phone: isPhone };
    }


    PhoneViewAR() {
        return(
            <div className='card2' style={{backgroundImage: this.props.img }}>
                <div className="DdivAR2"><h3>
                    <p>{this.calc(this.props.fat)} غ</p> دهون:
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>
                <div className='footerAR2'>
                    <h2>{this.props.arname}</h2><br></br>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5><br></br>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder='100' onChange={this.gramHandle} /> غرام</h6>
                </div>
            </div>
        )
    }

    DexViewAR() {
        return(
            <div className='card' style={{backgroundImage: this.props.img }}>
                <div className="DdivAR"><h3>
                    دهون:<p>{this.calc(this.props.fat)} غ</p> 
                    <br></br>سكريات:<p>{this.calc(this.props.carb)} غ</p>
                    <br></br>ألياف:<p>{this.calc(this.props.fiber)} غ</p>
                    <br></br>سكر:<p>{this.calc(this.props.sugar)} غ</p>
                    <br></br>بروتين:<p>{this.calc(this.props.protein)} غ</p>
                    </h3>
                </div>
                <div className='footerAR'>
                    <h4>{this.props.arname}</h4>
                    <h5>{this.calcKcal(this.props.cal)} سعرة حرارية</h5>
                    <h6>كل <span>{this.state.grams}</span><input id="gram" type="number" placeholder='100' onChange={this.gramHandle} /> غرام</h6>
                </div>
            </div>
        )
    }

    render() {        
        if(this.state.phone) {
            return(
                this.PhoneViewAR()  
            )
        } else {
            return(
                this.DexViewAR() 
            )
        }
    }

}

export default Arabic